.search{
    max-width: 450px;
    position: relative;
    top:-20px;
}
.search .form-inline{
    display: flex;
    justify-content: space-around;
}
.search input{
    width: 86%;
    height: 48px;
    line-height: 48px;
    border-radius: 3px;
    border:1px solid #ddd;
    padding: 0px 15px;
    border-radius: 3px 0px 0px 3px;

}
@media(max-width:767px){
    .search input{
    width: 80%; 
    }
}
.search .btn-primary{
    position: relative;
    left: -3px;
    border-radius: 0px 3px 3px 0px;
}
.search .btn-primary img{
    height: 24px;
    position: relative;
    top: -3px;
}